import logo from './logo.svg';
import React, { useState, useEffect } from 'react';
import { parse } from 'espree';
import './App.css';

function App() {
  const resultTextareaRef = React.createRef();
  const inputTextareaRef = React.createRef();
  const [finalResult, setFinalResult] = useState('');
  const [finalClass, setFinalClass] = useState('');

  const parseSnippet = (content, parseFn = parse, options = {
    range: true,
    loc: true,
    ecmaVersion: 6,
    comment: true,
  }) => {

    setFinalClass('');

    const fullContent = `const a = {${content}}`;
    let result = '    <HotTable\n';

    try {
      const parseResult = parseFn(fullContent, options);

      window.fullContent = fullContent;

      if (parseResult) {
        parseResult.body[0].declarations[0].init.properties.forEach((property) => {
          // console.log(property);
          result += `      ${property.key.name}=`;

          if (property.value.type === 'Literal' && typeof property.value.value === 'string') {
            result += `'${property.value.value}'`;
          } else {
            result += `{${fullContent.slice(...property.value.range)}}`
          }

          result += `\n`;
        });

        result += `    />`

        setFinalResult(result);
      }

    } catch (error) {
      console.log(error);
      setFinalResult('💩');
      setFinalClass('error');
    }
  };

  function copy() {
    // let copyText = document.createElement('TEXTAREA');
    // document.body.appendChild(copyText);
    // copyText.value = reactContent;
    resultTextareaRef.current.select();
    document.execCommand("copy");
  }

  useEffect(() => {
    parseSnippet(inputTextareaRef.current.value);
  });

  return (
    <div className="App">
      <div id='left-section'>
        <h1>Input</h1>
        <textarea ref={inputTextareaRef} onKeyUp={(event) => parseSnippet(event.target.value)} defaultValue={`\
// Paste just the settings, without the enclosing object.
// For example:

data: [
  ['Lorem', 'ipsum', 'dolor', 'sit', '12/1/2015', 23],
  ['adipiscing', 'elit', 'Ut', 'imperdiet', '5/12/2015', 6],
  ['Pellentesque', 'vulputate', 'leo', 'semper', '10/23/2015', 26],
  ['diam', 'et', 'malesuada', 'libero', '12/1/2014', 98],
  ['orci', 'et', 'dignissim', 'hendrerit', '12/1/2016', 8.5]
],
height: 'auto',
colHeaders: true,
rowHeaders: true,
filters: true,
colWidths: 100,
editor: false,
fillHandle: false,
licenseKey: 'non-commercial-and-evaluation',
`}></textarea>
      </div>
      <div id='right-section'>
        <h1>Output</h1>
        <textarea className={finalClass} readOnly={true} ref={resultTextareaRef} value={finalResult}></textarea><br/>
        <button onClick={(...args) => copy(...args)}>COPY RESULT</button>
      </div>

      <div id='info'>Version: 0.0.1-alpha, pozdro.</div>
    </div>
  );
}

export default App;

